body {
    background: var(--bg-light);
}
.NavigationWrapper{
    width: 100%;
    height: 120px;
    background: var(--bg-light);
}
.NavigatioItem{
    width: 300px;
    height: 100px;
    background:var(--light-color);
    border: 5px solid var(--dark-color);
    border-radius: 20px;
    cursor: pointer;
    text-align: center;
    font-size: 50px;
    font-weight: bold;
    line-height: 100px;
    color: var(--dark-color);
}

.HomePageMenuWrapper{
    width: 100%;
    height: 100vh;
    background: var(--bg-light);
    display: flex;
}
.GameWrapper{
    width: 400px;
    height: 100px;
    background:var(--light-color);
    border: 5px solid var(--dark-color);
    border-radius: 20px;
    margin: 15% auto;
    cursor: pointer;
    text-align: center;
    font-size: 60px;
    font-weight: bold;
    line-height: 100px;
    color: var(--dark-color);
}
.GameWrapper a, .NavigatioItem a{
    text-decoration: none;
    color: var(--dark-color);
}
.GameWrapper:hover, .NavigatioItem:hover{
    border: 5px solid var(--light-color);
    background: var(--dark-color);
}
.GameWrapper a:hover, .NavigatioItem a:hover{
    color: var(--light-color);
}

.menu{
    position: fixed;
    top: 10px;
    right: 50%;
    width: 80px;
    height: 80px;
    border: 5px solid var(--dark-color);
    border-radius: 50%;
    background: var(--bg-dark);
}
.menuItem{
    margin: 12px auto;
    width: 40px;
    height: 10px;
    background: var(--dark-color)
}
.menuItemInside{
    margin: 12px auto;
    width: 40px;
    height: 10px;
    background: var(--light-color)
}
.OpenedMenu{
    position: fixed;
    top: 293px;
    right: 40%;
    width: 500px;
    height: 600px;
    border: 5px solid var(--dark-color);
    border-radius: 20px;
    background: var(--bg-dark);
    cursor:default;
}
.CircleMenu{
    position: fixed;
    top: 10px;
    right: 50%;
    width: 80px;
    height: 80px;
    border: 5px solid var(--dark-color);
    border-radius: 50%;
    background: var(--bg-dark);
    cursor:pointer;
}
.OpenMenuWrapper{
    position: relative;
    margin: 90px auto;
    width: 260px;
    height: 480px;
}
.OpenMenuItem{
    width: 250px;
    height: 150px;
    margin: auto;
    cursor: pointer;
    text-align: center;
    font-size: 50px;
    font-weight: bold;
    line-height: 100px;
    text-decoration: none;
    color: var(--light-color);
}
.OpenMenuItem a{
     text-decoration: none;
     color: var(--light-color);
 }
.OpenMenuItem:hover, .OpenMenuItem a:hover{

    color: var(--dark-color)
}

