.containerCreator {
    width: 100px;
    height: 100px;
    margin: 0 auto;
    border: 5px solid blue;
}

.appWrapper {
    width: 1600px;
    min-height: 100px;
    margin: 120px auto;
    border: 5px solid var(--dark-color);
    border-radius: 20px;
    background: var(--bg-dark);
}

.TableWrapper {
    display: flex;
    width: 90%;
    min-height: 180px;
    margin: 10px auto;
    border: 5px solid var(--light-color);
    border-radius: 20px;
    background: var(--bg-light);
    justify-content: space-around;
}

.currentNumber {
    font-size: 100px;
    font-weight: bold;
    position: relative;
    top: 10px;
    left: 10px;
    width: 200px;
    height: 150px;

    border-radius: 20px;
    text-align: center;
    border: 5px solid var(--dark-color);
    background: var(--light-color);
    color: white;
    animation: neonForContainer 5s infinite;
}

.playingFieldWrapper {
    margin: 10px auto;
    width: 75%;
    min-height: 500px;
    border: 3px solid var(--dark-color);
    border-radius: 20px;
    display: flex;
    flex-wrap: wrap;
    text-align: center;
    background: var(--bg-dark);
}

.container {
    margin: 5px auto;
    width: 100px;
    height: 100px;
    border-radius: 20px;
    border: 5px solid var(--light-color);
    background: var(--dark-color);
    color: var(--light-color);
}

.container p, .coloredContainer p, .couponItem p {
    font-family: 'Pacifico', cursive;
    font-size: 50px;
    font-weight: bolder;
    line-height: 1px;
}
.BigCouponPart .couponItem p{
    font-size: 32px;
}

.coloredContainer {
    margin: 5px auto;
    border-radius: 20px;
    width: 100px;
    height: 100px;
    border: 5px solid var(--dark-color);
    background: var(--light-color);
    color: rgb(3, 57, 73);
}


.Table {
    position: relative;
    top: 10px;
    margin-left: 30px;
    width: 60%;
    min-height: 100px;
    border-top: 5px solid var(--light-color);
    border-radius: 20px;
}

.generateButton {
    position: relative;
    top: 10px;
    width: 300px;
    height: 156px;
    margin: 0 auto;
    border: #211f21 2px solid;
    border-radius: 20px;
}

.generateButton button {
    font-size: 40px;
    font-weight: bold;
    width: 300px;
    height: 156px;
    border: 5px solid var(--light-color);
    border-radius: 20px;
    background: var(--dark-color);
    color: var(--light-color);
}

.DroppedNumbers {
    display: flex;
    flex-wrap: wrap;
    width: 90%;
    min-height: 50px;
    margin: 0 auto;
    padding: 20px;
    text-align: center;
    line-height: 30px;
    font-size: 30px;
}

.DroppedNumberItem {
    line-height: 50px;
    width: 50px;
    height: 50px;
    margin: 1px;
    background: #211f21;
    border-radius: 40%;
    color: var(--bg-dark);
}

.DroppedNumberItem p {
    display: inline;
}

.LostDigits {
    width: 80px;
    height: 30px;
    border: 5px solid #5eff00;
}

@keyframes neonForContainer {
    0%, 39%, 41%, 59%, 61%, 100% {
        text-shadow: #0c0c0c 0 0 12px,
        #171511 0 0 24px,
        #171714 0 0 36px;
    }
    40%, 60% {
        text-shadow: #a69f0b 0 0 12px,
        rgb(0, 91, 92) 0 0 24px,
        rgb(0, 142, 143) 0 0 36px
    }
}
.closedCoupon{
    width: 1200px;
    height: 120px;
    margin: 50px auto;
}
.closedCoupon button{
    width: 470px;
    height: 100px;
    font-size: 40px;
    font-weight: bold;
    border: 5px solid var(--light-color);
    border-radius: 20px;
    background: var(--dark-color);
    color: var(--light-color);
    margin-left: 30%;
}


/*                  COUPON             */

.twoCoupons{
    margin: 50px auto;
    width: 1200px;
    min-height: 500px;
    border: 5px solid var(--dark-color);
    background: var(--bg-light);
    border-radius: 20px;
}
.couponWrapper {
    margin: 50px auto;
    width: 800px;
    min-height: 300px;
    background: var(--bg-light);
}
.BigCouponWrapper{
    margin: 50px 30px;
    width: 1000px;
    min-height: 500px;

    background: var(--bg-light);

}

.coupon {
    padding: 20px;
    display: flex;
    flex-wrap: wrap;
    margin: 30px auto;
    width: 80%;
    height: 70%;
    text-align: center;
    border: 5px solid var(--dark-color);
    border-radius: 20px;
}
.bigCoupon{
    display: flex;
    flex-wrap: wrap;
    margin: 30px auto;
    width: 960px;
    height: 70%;
    text-align: center;
    border: 5px solid var(--dark-color);
    border-radius: 20px;
}

.BigCouponPart{
    padding: 10px;
    margin: 20px auto;
    display: flex;
    flex-wrap: wrap;
    width: 70%;
    height: 220px;
}
.couponWrapper .coupon .couponItem {
    margin: 0 auto;
    width: 100px;
    height: 100px;
    border: 2px solid var(--light-color);
    border-radius: 50%;
}
 .bigCoupon .couponItem {
    margin: 0 auto;
    width: 70px;
    height: 70px;
    border: 2px solid var(--light-color);
    border-radius: 50%;
}

.bigCoupon  .blindItem {
    margin: 0 auto;
    width: 70px;
    height: 70px;
    border: 2px solid var(--light-color);
    background: var(--bg-light);
    color: var(--bg-light);
    border-radius: 50%;
}
.twoCoupons button{
    font-size: 40px;
    font-weight: bold;
    width: 500px;
    height: 80px;
    border: 5px solid var(--light-color);
    border-radius: 20px;
    background: var(--dark-color);
    color: var(--light-color);
    margin-top: 20px;
    margin-left: 18%;
}
.generateButton button:hover, .twoCoupons button:hover, .closedCoupon button:hover{
    border: 5px solid  var(--dark-color);;
    background: var(--light-color);
    color: var(--dark-color);
    cursor: pointer;
}
.blindItem {
    margin: 0 auto;
    width: 100px;
    height: 100px;
    border: 2px solid var(--light-color);
    background: var(--bg-light);
    color: var(--bg-light);
    border-radius: 50%;
}

.printWrapper{
    height: 297mm;
    width: 210mm;
    margin: 10mm 10mm 10mm 20mm;
    color: #000;
    box-shadow: none;
    text-shadow: none;
    font-size: 14px;
}
.couponButtons{
    width: 100%;
    height: 180px;
    display: flex;
}
.couponButtons button{
    width: 270px;
    height: 120px;
    margin-left: 100px;
}

@media (max-width: 1200px) {
    .appWrapper {
        width: 1200px;
    }
    .container, .coloredContainer {
        width: 75px;
        height: 75px;
    }
    .currentNumber {
        font-size: 70px;
        font-weight: bold;
        position: relative;
        top: 25px;
        left: 20px;
        width: 150px;
        height: 100px;
    }
    .TableWrapper {
        width: 900px;
        min-height: 150px;
    }
    .Table {
        top: 10px;
        width: 480px;
    }
    .generateButton{
        position: relative;
        top: 20px;
        width: 180px;
        height: 100px;
        margin: 0 auto;
        border: #211f21 2px solid;
        border-radius: 20px;
    }
    .generateButton button {
        font-size: 30px;
        width: 180px;
        height: 100px;
    }
    .container p, .coloredContainer p, .couponItem p{
        font-size: 36px;
    }
    .DroppedNumberItem {
        line-height: 40px;
        width: 40px;
        height: 40px;
    }
}
@media print {
    *{
        -webkit-print-color-adjust: exact !important;
        color-adjust: exact !important;
    }
    .TableWrapper , .playingFieldWrapper, .menu, button, .couponButtons {
        display: none;
    }
    .twoCoupons, .appWrapper{
        border: none;
    }
}